/**
* 图表界面
*/
<template>
  <!-- 组件主盒子 -->
  <div class="stbox">
    <el-table
      stripe
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="维度"
        width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <!--<p>姓名: {{ scope.row.name }}</p>-->
            <!--<p>住址: {{ scope.row.address }}</p>-->
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.name }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column :label="listTitle[0].titleName">
        <template slot-scope="scope">
          <el-input v-model="scope.row.one" placeholder="输入分数" @change="getCity()"></el-input>
        </template>
      </el-table-column>
      <el-table-column :label="listTitle[1].titleName">
        <template slot-scope="scope">
          <el-input v-model="scope.row.two" placeholder="输入分数" @change="getCity()"></el-input>
        </template>
      </el-table-column>
      <el-table-column :label="listTitle[2].titleName">
        <template slot-scope="scope">
          <el-input v-model="scope.row.three" placeholder="输入分数" @change="getCity()"></el-input>
        </template>
      </el-table-column>

      <el-table-column label="总成绩" prop="gradeAll">
      </el-table-column>
      <el-table-column label="平均成绩" prop="gradeAverage">
      </el-table-column>

    </el-table>
    <div>
      <div :span="12" class="text-c">
        <div class="paybox">
          <div class="cavasbox" ref="payNumEchart"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Chart from 'echarts'

  export default {
    name: "welcome",
    data() {
      return {
        input: '',
        gradeAll: [],//总成绩
        gradeAverage: [],//平均成绩
        listTitle: [
          {
            titleName: '第一学期',
            titleStock: 0.33,
          },
          {
            titleName: '第二学期',
            titleStock: 0.33,
          },
          {
            titleName: '第三学期',
            titleStock: 0.34,
          },
        ],
        tableData: [{
          name: '方案设计',
          one: '60',
          two: '70',
          three: '0',
          gradeAll: '',
          gradeAverage: 60,
        }, {
          name: '职业能力',
          gradeAll: '',
          one: '30',
          two: '70',
          three: '0',
          gradeAverage: 60,

        }, {
          name: '小组合作',
          gradeAll: '',
          one: '60',
          two: '70',
          three: '0',
          gradeAverage: 60,

        }, {
          name: '总结汇报',
          gradeAll: '',
          one: '60',
          two: '70',
          three: '',
          gradeAverage: 60,

        }, {
          name: '实验报告',
          gradeAll: '',
          one: '60',
          two: '70',
          three: '90',
          gradeAverage: 60,

        }],
        payNumoption: {

          title: {
            text: '王子龙成绩雷达图'
          },
          tooltip: {},
          legend: {
            data: ['总成绩', '平均成绩']
          },
          radar: {
            // shape: 'circle',
            name: {
              textStyle: {
                color: '#fff',
                backgroundColor: '#999',
                borderRadius: 3,
                padding: [3, 5]
              }
            },
            indicator: [
              {name: '方案设计', max: 100},
              {name: '职业能力', max: 100},
              {name: '小组合作', max: 100},
              {name: '总结汇报', max: 100},
              {name: '实验报告', max: 100},
            ]
          },
          series: [{
            name: '预算 vs 开销（Budget vs spending）',
            type: 'radar',
            label: {
              normal: {
                show: true,
                formatter: function (params) {
                  return params.value;
                }
              }
            },

            data: [
              {
                value: [0, 0, 0, 0, 0, 0],
                name: '总成绩',
                itemStyle: {
                  color: "#FFAD55"
                }

              },
              {
                value: [60, 60, 60, 60, 60,],
                name: '平均成绩',
                itemStyle: {
                  color: "#4F9EFF"
                }

              }
            ]
          }]
        },
      }
    },
    // 导入组件
    components: {
      // 点聚合组件
    },
    // 创建完毕状态(里面是操作)
    created() {

    },
    // 挂载结束状态(里面是操作)
    mounted() {
      var arr = [];
      this.tableData.forEach(key => {
        key.gradeAll = ((key.one * this.listTitle[0].titleStock) + (key.two * this.listTitle[1].titleStock) + (key.three * this.listTitle[2].titleStock)).toFixed(2);
        arr.push(key.gradeAll)
      })
      this.payNumoption.series[0].data[0].value = arr
      this.getpayNum()
    },
    // 里面的函数只有调用才会执行
    methods: {
      // 支付方式统计
      getpayNum() {
        this.chart = Chart.init(this.$refs.payNumEchart)
        this.chart.setOption(this.payNumoption)
      },
      //修改分数事件：
      getCity(ev) {
        console.log('ss')
        var arr = [];
        this.tableData.forEach(key => {
          key.gradeAll = ((key.one * this.listTitle[0].titleStock) + (key.two * this.listTitle[1].titleStock) + (key.three * this.listTitle[2].titleStock)).toFixed(2);
          arr.push(key.gradeAll)
        })
        this.payNumoption.series[0].data[0].value = arr
        this.getpayNum()
      }

    }
  };
</script>
<style>
  .stbox {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  .stbgc {
    background-color: #fff;
    height: 60px;
    line-height: 60px;
    border-radius: 5px;
    padding: 0px 16px;
  }

  .stsearch {
    text-align: center;
  }

  .text-c {
    margin: 20px auto 0;
  }

  .st-gbox {
    background-color: #fff;
    margin-top: 20px;
    border-radius: 5px;
    height: 30vh;
    box-sizing: border-box;
    padding: 10px;
  }

  .cavasbox {
    box-sizing: border-box;
    margin: auto;
    width: 1200px;
    height: 400px;
  }

  .paybox {
    width: 100%;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 5px;
    /*height: 32vh;*/
  }
</style>



